     // Ù‚Ø¯ ØªØ±ØºØ¨ ÙÙŠ ØªØ·Ø¨ÙŠÙ‚Ù‡ ÙÙ‚Ø· Ø®Ø§Ø±Ø¬ <pre>
     // processed = processed.replace(/\n/g, '<br>');


    return processed;
