     const welcomeMsg = this.getAttribute('welcome-message') || 'Ù…Ø±Ø­Ø¨Ù‹Ø§ Ø¨Ùƒ! ÙƒÙŠÙ ÙŠÙ…ÙƒÙ†Ù†ÙŠ Ù…Ø³Ø§Ø¹Ø¯ØªÙƒ Ø§Ù„ÙŠÙˆÙ…ØŸ';
