   messageEl.setAttribute('message-id', `user-${Date.now()}`);
   messageEl.textContent = content; // ØªØ¹ÙŠÙŠÙ† Ø§Ù„Ù…Ø­ØªÙˆÙ‰ Ù…Ø¨Ø§Ø´Ø±Ø©

